.page-toolbar {
  width: 40px;
  position: absolute;
  top: 50%;
  right: -40px;
  transform: translateY(-50%);
  height: unset;
  margin: 0;
  //@media screen and (max-width: 767px){
  //  height: auto;
  //  width: auto;
  //  background-color: #fff;
  //  left: 50%;
  //  right: auto;
  //  transform: translateX(-50%);
  //  bottom: -25px;
  //  top: auto;
  //}
  @media screen and (max-width: 475px){
    bottom: 40px;
  }
  @media screen and (max-width: 420px){
    bottom: 75px;
  }
  @media screen and (max-width: 375px){
    bottom: 110px;
  }
  .page-main {
    height: 100%;
    text-align: center;
    //@media screen and (max-width: 767px){
    //  background-color: #fff;
    //  box-shadow: 0 1px 3px 0 rgba(0,0,0,.2), 0 1px 1px 0 rgba(0,0,0,.14), 0 2px 1px -1px rgba(0,0,0,.12);
    //}

    ul {
      list-style-type: none;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      //@media screen and (max-width: 767px) {
      //  flex-direction: row;
      //  align-items: center;
      //  li{
      //    display: flex;
      //    padding: 5px 10px;
      //  }
      //}
      li {
        padding: 10px 0;
        @media screen and (max-width: 767px){
          padding: 5px 10px;
        }
        cursor: pointer;
        &:hover {
          i {
            opacity: 1;
          }
        }
        &.disabled {
          &:hover {
            i {
              opacity: 0.5;
            }
          }
        }

        i {
          font-size: 24px;
          opacity: 0.5;
        }
        span {
          font-size: 18px;
        }
      }
    }
  }
}